import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Card from './Card';
import IconPotionLarge from '../images/potion-large.svg';
import IconPotionSmall from '../images/potion-small.svg';
import IconPotionMedium from '../images/potion-medium.svg';

class Potion extends Component {
  static propTypes = {
    potionDrank: PropTypes.bool.isRequired,
    hp: PropTypes.number.isRequired,
    number: PropTypes.number.isRequired,
    updatePlayer: PropTypes.func.isRequired,
    handleClick: PropTypes.func.isRequired,
    suit: PropTypes.string.isRequired
  };

  centerPipsStyle(IconPotionLarge, IconPotionSmall) {
    switch (this.props.number) {
      case 2:
        return IconPotionSmall;
      case 3:
        return IconPotionSmall;
      case 4:
        return IconPotionSmall;
      case 5:
        return IconPotionMedium;
      case 6:
        return IconPotionMedium;
      case 7:
        return IconPotionMedium;
      case 8:
        return IconPotionLarge;
      case 9:
        return IconPotionLarge;
      case 10:
        return IconPotionLarge;
      default:
        return [];
    }
  }

  onClick = () => {
    if (!this.props.potionDrank) {
      const maxHeal = (this.props.number < 11) ? this.props.number : 11;
      let effect = this.props.hp + maxHeal;
      effect = (effect > 21) ? 21 : effect;
      this.props.updatePlayer({hp: effect, potionDrank: (this.props.potionLimit)});
    }
    this.props.handleClick({ suit: this.props.suit, number: this.props.number });
  }

  render() {
    return (
      <Card {...this.props}
            centerPip={this.centerPipsStyle(IconPotionLarge, IconPotionMedium, IconPotionSmall)}
            suit={this.props.suit}
            number={this.props.number}
            onClick={this.onClick} />
    );
  }
}

export default Potion;



